<template>
  <!-- 备选基础服务团队 -->
  <div class="group-select">
    <el-form-item :label="label" :prop="prop" :rules="rules">
      <el-select
        :value="value"
        @change="onValueChange"
        :placeholder="placeholder"
        :disabled="readonly"
        size="small"
        multiple
        clearable
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :value="item.value"
          :label="item.label"
          >{{ item.label }}</el-option
        >
      </el-select>
    </el-form-item>
  </div>
</template>
<script>
import { getList } from '@/api/FdServiceType'

export default {
  data() {
    return {
      optionValue: '',
      options: []
    }
  },
  props: {
    optionsData: {
      type: Array,
      default: () => []
    },
    readonly: {
      type: Boolean,
      default: false
    },
    selectDisable: {
      type: Boolean,
      default: false
    },
    value: {
      type: Array,
      default: () => []
    },
    moduleName: {
      type: String,
      default: '适用套餐'
    },
    label: {
      type: String,
      default: '适用套餐'
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    filterOption: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择适用套餐'
    }
  },
  watch: {
    value(val) {
      this.optionValue = val
    }
  },
  mounted() {
    if (this.optionsData.length) {
      this.options = this.optionsData
    } else {
      this.getGroups()
    }
  },
  methods: {
    getGroups() {
      const params = {
        show_history: 0
      }
      getList(params).then(res => {
        const { list } = res.data
        this.options = list.map(group => {
          return {
            value: group.batch_id,
            label: group.title
          }
        })
      })
    },
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
<style lang="scss">
.group-select {
  .el-select {
    width: 100%;
  }
}
</style>
