import request from '@/utils/request'

export function getList(query) {
  return request({
    url: '/fd/products',
    method: 'get',
    params: query
  })
}

export function addProducts(params) {
  return request({
    url: '/fd/products',
    method: 'post',
    data: params
  })
}
// 获取筛选条
export function getFilter(query) {
  return request({
    url: '/fd_service_type/filter',
    method: 'get',
    params: query
  })
}

export function getHistoryList(id, query) {
  return request({
    url: '/fd_service_type/' + id + '/logs',
    method: 'get',
    params: query
  })
}

// 11/api/fd_service_type/services 获取服务项目
export function getServices(id, query) {
  return request({
    url: '/fd_service_type/services',
    method: 'get',
    params: query
    // mock: `/fd_service_type/services`
  })
}

export function changeSaleStatus(id, params) {
  return request({
    url: `/fd/products/${id}/status`,
    method: 'put',
    data: params
  })
}

export function update(id, params) {
  return request({
    url: `/fd/products/${id}`,
    method: 'put',
    data: params
    // mock: `/fd_service_type/${payload._id}`
  })
}

export function getEditInfo(id) {
  return request({
    url: `/fd/products/${id}`,
    method: 'get'
    // mock: `/fd_service_type/${id}`
  })
}

// 上架替换同名套餐on_sale_id 上架id, 下架  off_sale_id
// /api/fd_service_type/confirm_change_sale_status
export function replaceSaleApi(params) {
  return request({
    url: '/fd_service_type/confirm_change_sale_status',
    method: 'put',
    data: params
  })
}
