<template>
  <div class="order-list">
    <div class="order-list__search">
      <el-button
        class="order-list__search__collapse-btn"
        type="text"
        @click="searchFormShow = !searchFormShow"
      >
        {{ searchFormShowText }}
        <i
          class="el-icon--right"
          :class="searchFormShow ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
        />
      </el-button>
      <el-collapse-transition>
        <search-form
          v-show="searchFormShow"
          :cur-order-status="curTabName === 'all' ? '' : curTabName"
          :local-form-data="LocalFormData"
          @search="handleSearch"
        />
      </el-collapse-transition>
    </div>
    <div class="add">
      <el-row>
        <el-button type="primary" size="mini" @click="AddMeal">
          新增权益
        </el-button>
      </el-row>
    </div>
    <div class="adminuser-list__body">
      <div slot="header" class="title">权益列表</div>
      <el-table
        :header-row-style="getHeadStyle"
        :data="tableData"
        border
        :default-sort="{ prop: 'in_service_user_num' }"
        empty-text="暂无数据，请尝试输入其他关键字"
        style="width: 100%;"
      >
        <el-table-column
          align="center"
          prop="no"
          label="权益编号"
          width="80"
          style="text-align: center;"
        />
        <el-table-column
          align="center"
          prop="name"
          label="权益名称"
          width="120"
          style="text-align: center;"
        />
        <el-table-column
          label="权益类型"
          align="center"
          min-width="100"
          prop="ask_mode"
        >
          <template slot-scope="scope">
            {{ scope.row.type | rightsType }}
          </template>
        </el-table-column>
        <el-table-column align="center" prop="price" min-width="100">
          <template slot="header">
            <span>权益内容</span>
          </template>
          <template slot-scope="scope">
            {{ rightsDetail(scope.row) }}
          </template>
        </el-table-column>
        <el-table-column align="center" min-width="280">
          <template slot="header">
            <span>适用套餐</span>
          </template>
          <template slot-scope="{ row }">
            {{ getProductList(row.scope_product) }}
          </template>
        </el-table-column>

        <el-table-column
          align="center"
          prop="create_user"
          label="适用人群"
          min-width="80"
        >
          <template slot-scope="{ row }">
            {{ row.scope_user | scopeUser }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="状态"
          min-width="100"
          key="8"
          prop="isOnServer"
        >
          <template slot-scope="scope">
            <div>
              <el-switch
                :value="scope.row.status"
                :width="60"
                :class="
                  scope.row.status === true ? 'online-style' : 'offline-style'
                "
                :active-text="scope.row.status ? '' : ''"
                :inactive-text="scope.row.status === true ? '上线' : '下线'"
                inactive-color="#C0C4CC"
                @change="changeOnlineStatusFun(scope.row, scope.row.status)"
              />
            </div>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="create_user"
          label="创建人"
          min-width="80"
        >
          <template slot-scope="{ row }">
            {{ row.creator.name }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="创建时间" min-width="110">
          <template slot-scope="scope">
            {{ new Date(scope.row.first_created_at) | formatTime() }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          min-width="120"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="handleClickEdit(scope.row)"
              :disabled="scope.row.status"
            >
              编辑</el-button
            >
          </template>
        </el-table-column>
        <template slot="empty">
          <div class="table-empty">
            暂无数据，请尝试输入其他关键字
          </div>
        </template>
      </el-table>
      <div class="adminuser-list__body__pagination">
        <el-pagination
          :current-page="page.currentPage"
          :page-sizes="pageSizes"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="putawayDialogVisible"
      width="30%"
      center
    >
      <span>{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="putawayDialogVisible = false">取 消</el-button>
        <el-button
          v-if="!!same_name_service_id"
          type="primary"
          @click="replaceSaleFun"
          >{{ dialogConfirmBtnTxt }}</el-button
        >
        <el-button v-else type="primary" @click="confirmShangjia">{{
          dialogConfirmBtnTxt
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getRightsList, updateServeStatus } from '@/api/marketing'
import SearchForm from './SearchForm'
// import moment from 'moment'
// import _ from 'lodash'
export default {
  components: { SearchForm },
  data() {
    return {
      searchVal: {},
      dialogTitle: '上架提示',
      dialogConfirmBtnTxt: '确认上架',
      dialogContent: '',
      putawayDialogVisible: false,
      LocalFormData: {},
      selectOptsMaps: {},
      curTabName: 'all',
      searchFormShow: true,
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 50, 100],
      tableData: [],
      same_name_service_id: '',
      activeId: '',
      changedStatus: ''
    }
  },
  computed: {
    searchFormShowText() {
      return this.searchFormShow ? '收起' : '展开搜索条件'
    }
  },
  created() {
    this.handlerGetList()
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'RightsAdd') {
      if (to.params.type === 'update') {
        to.meta.title = '编辑权益'
      }
    }
    next()
  },
  methods: {
    rightsDetail(row) {
      if (row.discount_mode === 'PRICE') {
        return `优惠${(row.discount_value / 100).toFixed(2)}元`
      } else if (row.discount_mode === 'PERCENT') {
        return `${(row.discount_value / 100).toFixed(2)}折`
      }
      return ''
    },
    getProductList(list) {
      const nameList = list.map(product => {
        return product.title + ' ' + product.sub_title
      })
      return nameList.join(',')
    },
    // 上下架
    changeOnlineStatusFun(row, type) {
      this.putawayDialogVisible = true
      this.activeId = row._id
      this.changedStatus = !row.status
      if (!row.status) {
        // 上架
        this.changedStatus = true
        this.dialogTitle = '上下线提示'
        this.dialogContent = row.name + '将上线，确认上线吗？'
        this.dialogConfirmBtnTxt = '确认上线'
      } else {
        this.changedStatus = false
        this.dialogTitle = '下线提示'
        this.dialogContent = row.name + '将下线，确认下线吗？'
        this.dialogConfirmBtnTxt = '确认下线'
      }
    },
    confirmShangjia() {
      const params = {
        status: this.changedStatus
      }
      if (!this.activeId) {
        return
      }
      updateServeStatus(this.activeId, params).then(res => {
        this.$message.success('操作成功')
        this.putawayDialogVisible = false
        this.handlerGetList()
      })
    },
    AddMeal() {
      this.$router.push({
        name: 'RightsAdd',
        params: {
          id: 'new',
          type: 'add'
        }
      })
    },
    handleSearch(val) {
      this.searchVal = val.searchParams
      this.page.pageSize = 10
      this.page.currentPage = 1
      this.handlerGetList(this.searchVal)
    },
    handlerGetList(query = {}) {
      const _query = { ...query }
      _query.pageSize = this.page.pageSize
      _query.currentPage = this.page.currentPage
      getRightsList(_query).then(result => {
        this.tableData = result.data.list
        this.page.total = result.data.page.total
      })
    },
    async handleCurrentChange(page) {
      this.page.currentPage = page
      this.handlerGetList(this.searchVal)
    },

    async handleSizeChange(pageSize) {
      this.page.currentPage = 1
      this.page.pageSize = pageSize
      this.handlerGetList(this.searchVal)
    },
    getHeadStyle() {
      return {
        fontSize: '14px',
        color: '#17233D',
        letterSpacing: 0
      }
    },
    handleClickEdit(row) {
      // 编辑
      this.$router.push({
        name: 'RightsAdd',
        params: {
          id: row._id,
          type: 'update'
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.is_old_style {
  margin-left: 3px !important;
}
.order-list {
  .title {
    padding: 10px;
    line-height: 18px;
    font-size: 18px;
    font-family: 'PingFangSC-Medium';
    color: #17233d;
    border: 1px solid #ebeef5;
    border-bottom: none;
  }
  .add {
    margin-bottom: 20px;
  }
  .el-badge__content {
    background: #f8747f;
    &.is-fixed {
      transform: translateY(-30%) translateX(100%) scale(0.7);
    }
  }
  .adminuser-list__body__pagination {
    padding: 30px 0 10px;
    text-align: center;
  }
  .el-tabs {
    &__header {
      margin-bottom: 0;
    }
    &__nav-wrap {
      padding-left: 30px;
      &::after {
        height: 1px;
      }
    }
    &__item {
      padding: 5px 16px;
      height: 50px;
      &.is-active {
        .el-button--text {
          color: #{$activeColor};
        }
      }
      .el-button--text {
        font-size: 14px;
        color: #{$textLightBlock};
        &.order-list__body__button--pending {
          color: #f8747f;
        }
      }
      .el-badge {
        padding: 0 16px;
        &__content {
          background: #f8747f;
          &.is-fixed {
            transform: translateY(10%) translateX(50%) scale(0.7);
          }
        }
      }
    }
  }
  .el-dialog__body {
    line-height: 20px;
  }
  &__body {
    &__table {
      &__trade-Status,
      &__source,
      &__user,
      &__after-sale-status {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        line-height: 1;
        flex-direction: column;
      }
      &__trade-Status {
        min-height: 70px;
        &__close-btn {
          margin-left: 0;
        }
        .el-button--text {
          padding-top: 5px;
          padding-bottom: 0;
        }
      }
      &__source__weapp-scene,
      &__source__weapp-scene:hover {
        text-decoration: underline;
      }
      &__source__custom-scene,
      &__user__see-btn {
        padding-top: 5px;
        transform: scale(0.84);
      }
      &__user__see-btn {
        padding-bottom: 0;
        font-size: 14px;
        color: #{$textLightBlock};
      }
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';

.order-list {
  .cherry {
    color: #f8747f;
  }
  &__search {
    position: relative;
    padding-bottom: 30px;
    &__collapse-btn {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 99;
    }
  }
  &__body {
    border: 1px solid #ebeef5;
    border-radius: 4px;
    &__pagination {
      padding: 50px 0 50px;
      text-align: center;
    }
    &__checkbox-hide {
      padding: 30px 20px 20px;
      color: #409eff;
    }
  }
  &__content {
    padding: 0 20px;
  }
}
</style>
