import request from '@/utils/request'

export function getRightsList(params) {
  return request({
    url: '/trade/quanyi',
    method: 'get',
    params: params
    // mock: true
  })
}

export function addRights(data) {
  return request({
    url: '/trade/quanyi',
    method: 'post',
    data: data
    // mock: true
  })
}

export function getRightsOne(id) {
  return request({
    url: `/trade/quanyi/${id}`,
    method: 'get'
    // mock: true
  })
}

export function updateRights(id, params) {
  return request({
    url: `/trade/quanyi/${id}`,
    method: 'put',
    data: params
  })
}

export function changeSaleStatus(id, params) {
  return request({
    url: `/fd/products/${id}/status`,
    method: 'put',
    data: params
  })
}

export function updateServeStatus(id, status) {
  return request({
    url: `/trade/quanyi/${id}/status`,
    method: 'put',
    data: status
    // mock: true
  })
}
// 获取优惠券列表
export function getList(params) {
  return request({
    url: '/discount_coupon/list',
    method: 'get',
    params: params
    // mock: true
  })
}
// 获取列表筛选
export function getFilterApi() {
  return request({
    url: '/discount_coupon/filter',
    method: 'get'
  })
}
// 获取优惠券创建信息
export function getCouponCreateInfo() {
  return request({
    url: '/discount_coupon/create_info',
    method: 'get'
    // mock: true
  })
}
// 新增优惠券
export function addCoupon(data) {
  return request({
    url: '/discount_coupon',
    method: 'post',
    data: data
    // mock: true
  })
}
// 生成小程序码
// getCodeImg
export function getCodeImg(id) {
  return request({
    url: `discount_coupon/${id}/create_qrcode`,
    method: 'put'
  })
}
// 使失效
export function changeStatus(params) {
  return request({
    url: '/discount_coupon/' + params._id + '/change_status',
    method: 'put',
    data: params
  })
}
