<template>
  <div class="search">
    <el-form
      ref="searchTemplateForm"
      label-position="left"
      label-width="76px"
      :model="formData"
    >
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="权益名称" prop="name">
            <el-input
              v-model.number="formData.name"
              placeholder="请输入权益名称"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="权益类型" prop="type">
            <el-select
              v-model="formData.type"
              placeholder="请选择权益类型"
              size="small"
              clearable
            >
              <el-option
                v-for="item in $store.state.dictionary.rightsType"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <product-select
            v-model="formData.scope_product"
            :prop="'scope_user'"
          ></product-select>
        </el-col>
        <el-col :span="6">
          <el-form-item label="适用人群" prop="scope_user">
            <el-select
              v-model="formData.scope_user"
              multiple
              placeholder="请选择适用人群"
              size="small"
              clearable
            >
              <el-option
                v-for="item in $store.state.dictionary.scopeUser"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="7">
          <el-form-item label="创建时间" prop="createTime">
            <el-date-picker
              v-model="formData.createTime"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              prefix-icon="el-icon-date"
              :default-time="['12:00:00']"
              size="small"
              clearable
            >
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="状态" prop="status">
            <el-select
              v-model="formData.status"
              placeholder="请选择套餐状态"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.saleStatusTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-button type="primary" size="small" @click="handleSearch"
            >搜索</el-button
          >
          <el-button type="primary" plain size="small" @click="handleReset"
            >重置</el-button
          >
        </el-col>
      </el-row>
      <el-row :gutter="40"> </el-row>
    </el-form>
  </div>
</template>
<script>
import ProductSelect from '@/components/Select/ProductsSelect'
const defaultformData = {
  name: '',
  type: '',
  scope_user: [],
  scope_product: [],
  createTime: []
}

export default {
  props: {
    localFormData: {
      type: Object,
      default: () => {}
    },
    selectOptsMaps: {
      type: Object,
      required: false,
      // 套餐名称name 服务周期duration 定价price 创建时间 创建人create_user 首次上架时间 状态
      default: () => ({
        nameTypes: [],
        durationTypes: [],
        createUserTypes: [],
        priceType: [],
        saleStatusTypes: [
          {
            label: '上线',
            value: 1
          },
          {
            label: '下线',
            value: 0
          }
        ]
      })
    }
  },
  components: {
    ProductSelect
  },
  data() {
    var checkId = (rule, value, callback) => {
      setTimeout(() => {
        if (value < 0) {
          callback(new Error('编号必须大于0'))
        } else {
          callback()
        }
      }, 1000)
    }
    return {
      formData: {},
      searchTemplateForm: {
        id: [{ validator: checkId, trigger: 'blur' }]
      }
    }
  },
  watch: {},
  created() {
    this.formData = Object.assign({}, defaultformData, this.localFormData)
  },
  methods: {
    handleSearch() {
      this.$refs['searchTemplateForm'].validate(valid => {
        if (valid) {
          const params = {}
          if (this.formData.name) {
            params.name = this.formData.name
          }
          if (this.formData.type) {
            params.type = this.formData.type
          }
          if (this.formData.scope_user.length > 0) {
            params.scope_user = this.formData.scope_user.join(',')
          }
          if (this.formData.scope_product.length > 0) {
            params.scope_product = this.formData.scope_product.join(',')
          }
          if (this.formData.status !== '') {
            params.status = this.formData.status
          }
          if (this.formData.createTime && this.formData.createTime.length > 0) {
            params.first_created_start = this.formData.createTime[0]
            params.first_created_end = this.formData.createTime[1]
          }
          this.$emit('search', { searchParams: params })
        } else {
          return false
        }
      })
    },
    handleReset() {
      this.formData = Object.assign({}, defaultformData)
      this.handleSearch()
    }
  }
}
</script>

<style lang="scss">
.search {
  &__form {
    margin-bottom: -20px;
    .el-form-item {
      margin-bottom: 20px;
      &__label,
      &__content {
        height: 32px;
        line-height: 32px;
      }
    }
    .el-date-editor,
    .el-select {
      width: 100%;
    }
  }
}
</style>

<style lang="scss" scoped>
.search {
  &__form {
    &__source {
      display: flex;
      align-items: center;
    }
    &__source-scene,
    &__source-custom-value {
      width: 50%;
    }
    &__source-scene {
      padding-right: 14px;
    }
    &__source-custom-value {
      padding-left: 14px;
    }
  }
}
</style>
